@import url("https://fonts.googleapis.com/css2?family=Jomolhari&family=Roboto");
@import url("https://fonts.googleapis.com/css2?family=Noto+Naskh+Arabic&display=swap");

a {
  text-decoration: none !important;
}

:lang(ug) {
  font-family: "Noto Naskh Arabic", serif;
}

:lang(en) {
  font-family: Roboto, sans-serif;
}

.margin-y-1 {
  margin-top: 1em !important;
  margin-bottom: 1em !important;
}

.main-panel {
  font-size: 200%;
}

.main-panel input[type="text"] {
  font-size: 75% !important;
}

.left-panel {
  position: absolute;
  top: 10px;
  left: 0;
}

.right-panel {
  position: absolute;
  top: 20px;
  right: 20px;
  text-align: right;
}

.bottom-panel {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 0.9rem;
  line-height: 1.2;
  margin-bottom: 2.5em;
}

.bottom-panel p {
  margin-bottom: 0.5em;
}

.round {
  box-sizing: border-box;
  width: 2.5em !important;
  height: 2.5em !important;
  border-radius: 80em !important;
  text-align: center !important;
  padding: 0 !important;
}

.highlight span:nth-child(2n) {
  color: #0984e3;
}

.highlight span:nth-child(2n + 1) {
  color: #e84393;
}

.highlight span:hover {
  color: white !important;
}
